<template>
  <div class="article" v-if="model">
    <div class="d-flex py-3 px-2 border-bottom">
      <i class="iconfont icon-menu"></i>
      <strong class="flex-1">{{model.title}}</strong>
      <div class="text-grey fs-xs">{{model.updatedAt | date}}</div>
    </div>
    <div v-html="model.body" class="px-3 body text-lg py-3"></div>
    <div class="px-3 border-top py-3">
      <div class="d-flex ao-center">
        <i class="iconfont icon-menu"></i>
        <strong class="text-info fs-lg ml-1">相关资讯</strong>
      </div>
      <div class="pt-1">
        <router-link
          class="py-2 mt-2"
          tag="div"
          :to="`/articles/${item._id}`"
          v-for="item in model.related"
          :key="item._id"
        >{{item.title}}</router-link>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    id: {
      required: true
    }
  },
  watch:{
    id:'fetchArticle'
  },
  data() {
    return {
      model: {}
    }
  },
  created() {
    this.fetchArticle()
  },
  methods: {
    async fetchArticle() {
      const res = await this.$http({ url: `/articles/${this.id}`, method: 'get' })
      this.model = res;
    }
  }
}
</script>
<style lang="scss">
.article {
  .icon-menu {
    font-size: 1.5385rem;
  }
  .body {
    img {
      max-width: 100%;
    }
    iframe {
      max-width: 100%;
      height: auto;
    }
  }
}
</style>
