<template>
  <m-card :icon="icon" :title="title">
    <div class="nav jc-between">
      <div
        class="nav-item test"
        :class="{active : active === i}"
        v-for="(item,i) in categories"
        :key="i"
        @click="$refs.list.swiper.slideTo(i)"
      >
        <div class="nav-link">{{item.name}}</div>
      </div>
    </div>
    <swiper class="pt-3" :options="{autoHeight:true}" ref="list" @slide-change="()=>active = $refs.list.swiper.realIndex">
      <swiper-slide v-for="(i,k) in categories" :key="k">
        <slot name="items" :category="i"></slot>
      </swiper-slide>
    </swiper>
  </m-card>
</template>

<script>
export default {
  props: {
    title: {
      type: String, required: true
    },
    icon: {
      type: String, required: true
    },
    categories: {
      type: Array, required: true
    }
  },
  data() {
    return {
      active: 0
    };
  },
  mounted() {
  }
};

</script>
