let baseUrl = '';

switch (process.env.NODE_ENV) {
  case 'production':
    baseUrl = "http://mmst.szrrjc.com/mmst-wechat/" //生产环境url
    break
  case 'development':
    baseUrl = "http://localhost:3001/web/api/"
    break
  case 'local':
    baseUrl = "http://localhost:3001/web/api/"
    break
}
export {
  baseUrl
}