import Vue from 'vue'

const http = function (config) {
  return new Promise((resolve) => {
    Vue.prototype.$axios({
      method: config.method || 'post',
      url: config.url,
      data: config.data
    }).then(res => {
      if (res) {
        if (res.status == '200') {
          if (config.needAllStatus) {
            resolve(res.data)
          } else {
            let data = res.data;
            resolve(data)
          }
        }
      }
    })
  })
}

export default http