<template>
  <div class="home">
    <swiper :options="swiperOption">
      <swiper-slide>
        <img class="w-100" src="../assets/images/d33f5c02ab295d31ce80ab85d11fa75d.gif" alt />
      </swiper-slide>
      <swiper-slide>
        <img class="w-100" src="../assets/images/d798304fe8f38a6aab84a7f92e295dc5.jpeg" alt />
      </swiper-slide>
      <swiper-slide>
        <img class="w-100" src="../assets/images/d33f5c02ab295d31ce80ab85d11fa75d.gif" alt />
      </swiper-slide>
      <div class="swiper-pagination pagination-home text-right px-3 pd-2" slot="pagination"></div>
    </swiper>
    <!-- end of swiper -->
    <div class="nav-icons bg-white mt-3 text-center pt-3 text-dark-1">
      <div class="d-flex flex-wrap">
        <div class="nav-item mb-3" v-for="n in 10" :key="n">
          <i class="sprite sprite-news"></i>
          <div class="py-2">爆料站</div>
        </div>
      </div>
      <div class="bg-light py-2 fs-sm">
        <i class="sprite sprite-arrow mr-1"></i>
        <span>收起</span>
      </div>
    </div>
    <!-- end of nav icons -->
    <m-list-card icon="Menu" title="新闻资讯" :categories="newCats">
      <template #items="{category}">
        <router-link
          tag="div"
          :to="`/articles/${item._id}`"
          class="py-2 fs-lg d-flex"
          v-for="(item,i) in category.newsList"
          :key="i"
        >
          <span class="text-info">[{{item.categoryName}}]</span>
          <span class="px-2">|</span>
          <span class="flex-1 text-dark-1 text-ellipsis pr-2">{{item.title}}</span>
          <span class="text-grey-1 fs-sm">{{item.createdAt | date}}</span>
        </router-link>
      </template>
    </m-list-card>
    <m-list-card icon="card-hero" title="英雄列表" :categories="heroCats">
      <template #items="{category}">
        <div class="d-flex flex-wrap" style="margin:0 -0.5rem">
          <router-link
            tag="div"
            :to="`/heroes/${item._id}`"
            class="p-2 text-center"
            style="width:20%"
            v-for="(item,i) in category.heroList"
            :key="i"
          >
            <img :src="item.avatar" class="w-100" />
            <div>{{item.name}}</div>
          </router-link>
        </div>
      </template>
    </m-list-card>
  </div>
</template>

<script>
// @ is an alias to /src
import dayjs from 'dayjs'
export default {
  name: 'home',
  data() {
    return {
      swiperOption: {
        pagination: {
          el: '.pagination-home'
        }
      },
      newCats: [],
      heroCats: []
    }
  },
  created() {
    this.fetchNewsCats()
    this.fetchHeroesCats()
  },
  methods: {
    async fetchNewsCats() {
      const res = await this.$http({ url: 'news/list', method: 'get' })
      this.newCats = res
    },
    async fetchHeroesCats() {
      const res = await this.$http({ url: 'heroes/list', method: 'get' })
      this.heroCats = res
    }
  }
}
</script>
